@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.logined-user{padding-bottom:20px}.logined-user__img{margin:auto;margin-top:20px;margin-bottom:17px;width:70px;height:70px}.logined-user__title{text-align:center;color:#fffefe;font-size:18px;font-weight:400}.logined-user__location{margin-bottom:12px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;font-size:12px;color:#b3e5fc}.logined-user__locator{margin-right:8px}.logined-user__button{display:block;margin:auto;width:80px;height:25px;font-size:12px;color:#fffefe;background-color:#03a9f4;border:1.2px #fffefe solid;border-radius:10px;transition:all 1s}.logined-user__button:hover{background-color:#0288d1;border-color:transparent}

.nav{width:246px;background-color:#03a9f4}.nav__link{height:50px;padding-left:28px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;text-decoration:none;font-size:14px;color:#fffefe}.nav__link--is-active{background-color:#0288d1;border-left:4px #ffc107 solid}.nav__link-img{height:24px;margin-right:30px}.nav__button-box{margin-top:220px;padding-left:25px;padding-bottom:20px;padding-right:25px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}@media screen and (max-width: 1000px){.nav{position:absolute;top:59px;z-index:400}}

.announcement{width:61%;min-width:500px;height:270px;background-color:#fff}.announcement__body{height:210px;overflow-y:scroll;scroll-behavior:smooth}.head{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;height:60px;padding:10px 24px;border:1.5px #eee solid}.head__title{font-size:18px;color:#0288d1;font-weight:500}.head__text{color:#03a9f4;font-size:12px}.head__link{margin-left:25px;padding-left:5px;text-decoration:none;color:#727272;font-size:14px;transition:all 1s}.head__link--right{border-left:3px #ffc107 solid}.head__link--left{border-left:3px #03a9f4 solid}.head__link:hover{text-decoration:underline}.react-calendar{border:1.5px solid #eee}.item{display:-webkit-flex;display:flex;padding-top:12px;height:60px;border-bottom:1px #eee solid}.item--yellow{border-left:6px #ffc107 solid}.item__text{color:#8f8d8d}.item__img{margin:auto}.item--blue{border-left:6px #03a9f4 solid}.item__viewed{text-align:center;width:72px}

.people-list__item{position:relative;display:-webkit-flex;display:flex;padding-top:10px;padding-left:14px;padding-bottom:7px;border-bottom:1px #eee solid;border-right:1px #eee solid;border-left:1px #eee solid;background-color:#fff}.people-list__img{padding-right:9px}.people-list__title{padding:0;margin:0;margin-bottom:4px;line-height:14px;font-size:14px;color:#212121}.people-list__text{padding:0;margin:0;margin-bottom:2px;line-height:12px;font-size:14px;color:#212121}.people-list__when-was-last-time{line-height:10px;font-size:10px;color:#b6b6b6}.people-list__circle{position:absolute;top:13px;left:14px;height:10px;width:10px;border-width:1px;border-color:#fff;border-style:solid;border-radius:50%}.people-list__circle--online{background-color:#cddc39}.people-list__circle--ofline{background-color:#f4511e}

.courses-list__item{display:-webkit-flex;display:flex;border-bottom:1px #eee solid;border-right:1px #eee solid;border-left:1px #eee solid}.courses-list__rating{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-end;justify-content:flex-end;height:58px;width:74px;padding-left:16px;padding-right:16px;padding-bottom:7px;padding-top:7px}.courses-list__diagram--yellow{background-color:#ffc107}.courses-list__diagram--blue{background-color:#26c6da}.courses-list__diagram--red{background-color:#ef5350}.courses-list__rating-per{color:#727272;font-size:12px;line-height:15px;text-align:center}.courses-list__content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}.courses-list__text{color:#727272}.courses-list__title{font-size:14px}

.page{position:relative;width:27%;height:300px;background-color:white}.page__head{height:63px;padding:16px;background-color:#03a9f4}.page__title{font-size:18px;color:#fff}.page__text{font-size:12px;color:#fff}.page__button{position:absolute;top:40px;right:10px}

.history-small{display:-webkit-flex;display:flex;border-left:1px #eee solid}.history-small:first-child,.history-small:nth-child(3){margin-bottom:25px}.history-small:first-child::before{position:absolute;top:25px;left:14px;content:"";height:10px;width:10px;background-color:#03a9f4;border-radius:50%}.history-small:nth-child(2){position:relative}.history-small:nth-child(2)::before{position:absolute;top:25px;left:-27px;content:"";height:10px;width:10px;background-color:#ffc107;border-radius:50%}.history-small:last-child{position:relative}.history-small:last-child::before{position:absolute;top:25px;left:-27px;content:"";height:10px;width:10px;background-color:#26c6da;border-radius:50%}.history-small__box{position:relative;z-index:5;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:30px;width:30px;margin-bottom:1px;color:white}.history-small__content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;padding-left:12px;width:100%;border-top:1px #eee solid;border-bottom:1px #eee solid;border-right:1px #eee solid}.history-small__code{color:#727272}

.histories{position:relative;width:27%;padding-left:40px}.histories::before{position:absolute;top:30px;left:18px;content:" ";height:calc(100% - 60px);width:1px;background-color:#dbdada}

.main{height:716px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;box-sizing:border-box;padding-top:30px;padding-bottom:56px;background-color:#fafafa}.main__calendar-button{position:absolute;top:70px;right:65px}.main__up{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;margin-bottom:35px}.main__down{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around}.circle-button{z-index:100;width:40px;height:40px;border-radius:50%;border-color:transparent;font-size:15px;color:#fff;background-color:#ffc107;transition:all 1s}.circle-button:hover{background-color:#d86609}.react-calendar{width:27%}

.app{display:-webkit-flex;display:flex}

.header{position:relative;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:calc(100vw - 214.7px);height:60px;background-color:#0288d1}.header--big{width:100vw}.header__logo{position:absolute;top:13px;right:23px}.header__button{margin-left:30px;margin-right:25px;background-color:transparent;border:0;cursor:pointer}.header__window{font-size:18px;color:#fffefe}

*,*::before,*::after{box-sizing:border-box;outline:none}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd{margin:0}body{min-height:100vh;scroll-behavior:smooth;text-rendering:optimizeSpeed;line-height:1.5}ul,ol{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*{margin-top:1em}input,button,textarea,select{font:inherit}ul{margin:0;padding:0}

*{font-family:"Roboto", sans-serif;font-size:12px}

